<template>
  <div>
    <!--面包屑导航-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>隔离人员管理</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/divide' }">隔离人员详情</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--搜索头部input框-->
      <el-row :gutter="25">
        <el-col :span="8">
          <el-input v-model="userName" class="input-with-select" clearable placeholder="请输入内容"
                    @clear="getDivideList">
            <el-button slot="append" icon="el-icon-search" @click="getDivideListByCondition"></el-button>
          </el-input>
        </el-col>
        <el-col :span="8">
          <el-select v-model="searchValues" multiple placeholder="请选择" style="width: 230px">
            <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
          </el-select>
          <el-button round type="primary" @click="getDivideListByCondition">查询</el-button>
        </el-col>
        <el-col :span="2">
          <el-button type="primary" @click="addVisible=true">添加数据</el-button>
        </el-col>
      </el-row>
      <el-table
          :cell-style="rowClass"
          :data="DataList"
          :header-cell-style="headClass"
          :row-style="rowStyle"
          border
          style="width: 100%">
        <el-table-column
            label="序号"
            type="index">
        </el-table-column>
        <el-table-column
            label="姓名"
            prop="username">
        </el-table-column>
        <el-table-column
            label="电话"
            prop="mobile"
            width="100px">
        </el-table-column>
        <el-table-column
            label="体温"
            prop="tw">
        </el-table-column>
        <el-table-column
            label="症状1"
            prop="status1">
        </el-table-column>
        <el-table-column
            label="症状2"
            prop="status2">
        </el-table-column>
        <el-table-column
            label="症状3"
            prop="status3">
        </el-table-column>
        <el-table-column
            label="隔离地点"
            prop="area"
            width="160px">
        </el-table-column>
        <el-table-column
            label="备注"
            prop="bz">
        </el-table-column>
        <el-table-column
            label="创建时间"
            prop="createTime"
            width="180px">
        </el-table-column>
        <el-table-column
            label="操作"
            prop="salt"
            width="135px">
          <template slot-scope="scope">
            <el-tooltip class="item" content="编辑" effect="dark" placement="top-start">
              <el-button circle icon="el-icon-edit" size="mini" type="primary"
                         @click="editEvent(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" content="删除" effect="dark" placement="top-start">
              <el-button circle icon="el-icon-delete" size="mini" type="danger"
                         @click="removeEvent(scope.row.userId)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" content="转为治愈" effect="dark" placement="top-start">
              <el-button circle icon="el-icon-plus" size="mini" type="primary"
                         @click="changebz(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--分页功能-->
      <el-pagination
          :current-page="pageNum"
          :page-size="pageSize"
          :page-sizes="[5,8,11,12]"
          :total="total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange">
      </el-pagination>
    </el-card>
    <!--添加dialog对话框-->
    <el-dialog
        :visible.sync="addVisible"
        title="添加数据"
        width="35%"
        @close="closeAddDialog"
    >
      <!--这是内容区域-->
      <el-form ref="addFormRef" :model="Divide" :rules="addFormRules" class="demo-user" label-width="100px">
        <el-form-item label="姓名" prop="username">
          <el-input v-model="Divide.username"></el-input>
        </el-form-item>
        <el-form-item label="电话：" prop="mobile">
          <el-input v-model="Divide.mobile"></el-input>
        </el-form-item>
        <el-form-item label="隔离地点：" prop="area">
          <el-input v-model="Divide.area"></el-input>
        </el-form-item>
        <el-form-item label="体温：" prop="tw">
          <el-input v-model="Divide.tw" style="width: 100px;float:left"></el-input>
          <div style="float:left">°C</div>
        </el-form-item>
        <el-form-item label="症状1：" style="float:left">
          <el-checkbox v-model="Divide.status1" prop="status1" true-label="咳嗽：">咳嗽</el-checkbox>
        </el-form-item>
        <el-form-item label="症状2：" style="float:left">
          <el-checkbox v-model="Divide.status2" prop="status2" true-label="感冒">感冒</el-checkbox>
        </el-form-item>
        <el-form-item label="症状3：" style="float:left">
          <el-checkbox v-model="Divide.status3" prop="status3" style="float:left" true-label="发烧">发烧</el-checkbox>
        </el-form-item>
        <el-form-item label="备注：" prop="bz">
          <el-input v-model="Divide.bz"></el-input>
        </el-form-item>
        <el-form-item label="隔离日期：" prop="createTime">
          <el-date-picker
              v-model="Divide.createTime"
              placeholder="选择日期时间"
              size="large"
              style="float: left"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <el-button @click="addVisible = false">取 消</el-button>
      <el-button type="primary" @click="ConfirmAdd">确 定</el-button>
    </el-dialog>

    <!--修改dialog对话框-->
    <el-dialog
        :visible.sync="editVisible"
        title="修改数据"
        width="35%"
        @close="closeEditDialog"
    >
      <!--这是内容区域-->
      <el-form ref="addFormRef" :model="Divide" :rules="addFormRules" class="demo-user" label-width="100px">
        <el-form-item label="姓名" prop="username">
          <el-input v-model="Divide.username"></el-input>
        </el-form-item>
        <el-form-item label="电话" prop="mobile">
          <el-input v-model="Divide.mobile"></el-input>
        </el-form-item>
        <el-form-item label="隔离地区" prop="area">
          <el-input v-model="Divide.area"></el-input>
        </el-form-item>
        <el-form-item label="体温" prop="tw">
          <el-input v-model="Divide.tw"></el-input>
        </el-form-item>
        <el-form-item label="症状1：" style="float:left">
          <el-checkbox v-model="Divide.status1" prop="status1" true-label="咳嗽：">咳嗽</el-checkbox>
        </el-form-item>
        <el-form-item label="症状2：" style="float:left">
          <el-checkbox v-model="Divide.status2" prop="status2" true-label="感冒">感冒</el-checkbox>
        </el-form-item>
        <el-form-item label="症状3：" style="float:left">
          <el-checkbox v-model="Divide.status3" prop="status3" style="float:left" true-label="发烧">发烧</el-checkbox>
        </el-form-item>
        <el-form-item label="备注" prop="bz">
          <el-input v-model="Divide.bz"></el-input>
        </el-form-item>
        <el-form-item label="隔离日期" prop="createTime">
          <el-date-picker
              v-model="Divide.createTime"
              placeholder="选择日期时间"
              size="large"
              style="float: left"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <el-button @click="editVisible = false">取 消</el-button>
      <el-button type="primary" @click="ConfirmUpdate">确 定</el-button>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Divide",
  data() {
    //验证手机号码的规则
    const checkMobile = (rule, value, cb) => {
      const regMobile = /^1[345789]\d{9}$/
      if (regMobile.test(value)) {
        return cb()
      }
      //返回一个错误提示
      cb(new Error('请输入合法的手机号码'))
    };
    return {
      userName: null,
      addVisible: false,
      editVisible: false,
      DataList: [],
      Divide: {},
      addFormRules: {
        mobile: [
          {validator: checkMobile, trigger: 'blur'}
        ]
      },
      pageNum: 1,
      pageSize: 5,
      total: null,
      searchValues: [],
      options: [{
        value: '咳嗽',
        label: '咳嗽'
      }, {
        value: '感冒',
        label: '感冒'
      }, {
        value: '发烧',
        label: '发烧'
      }],
    }
  },
  methods: {
    rowStyle({row}) {
      if (row.status3 === "发烧") {
        return {
          backgroundColor: '#e9f1f6'
        }
      }
    },
    headClass() {  //表头居中显示
      return "text-align:center"
    },
    rowClass() { //表格数据居中显示
      return "text-align:center"
    },
    getDivideList() {
      this.$axios.get('/admin/divide/getDivideList', {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
        }
      }).then(res => {
        if (res.data.status === 10000) {
          this.DataList = res.data.data.list;
          this.total = res.data.data.total;
        }
      })
    },
    handleSizeChange(newSize) {
      this.pageSize = newSize;
      if (this.searchValues.length === 0) {
        this.getDivideList();
      } else {
        this.getDivideListByCondition();
      }
    },
    handleCurrentChange(newNum) {
      this.pageNum = newNum;
      if (this.searchValues.length === 0) {
        this.getDivideList();
      } else {
        this.getDivideListByCondition();
      }
    },
    getDivideListByCondition() {
      const query = {}
      query.pageNum = this.pageNum;
      query.pageSize = this.pageSize;
      query.searchValues = this.searchValues;
      if (this.userName != null) {
        if (this.userName.substr(0, 4) == "华帝王朝") {
          query.address = this.userName;
        } else if (this.userName.substr(0, 1) == 1) {
          query.mobile = this.userName;
        } else {
          query.username = this.userName;
        }
      }
      this.$axios.post('/admin/divide/getDivideListByCondition', query).then(res => {
        if (res.data.data.list !== undefined) {
          if (res.data.status === 10000) {
            this.DataList = res.data.data.list;
            this.total = res.data.data.total;
            this.$message.success("查询成功！")
            this.userName = null;
          } else {
            this.$message.error("查询失败！")
          }
        } else {
          this.DataList = [];
          const test = res.data.data;
          this.DataList.push(test);
          this.total = 1;
          this.userName = null;
        }
      })
    },
    ConfirmAdd() {
      this.Divide.tw = this.Divide.tw + "°C";
      this.$axios.post('/divide/add', this.Divide).then(res => {
        if (res.data.status === 10000) {
          this.$message.success("新增成功！")
          this.getDivideList();
        } else {
          this.$message.error("新增失败！")
        }
        this.addVisible = false;
      })
    },
    closeAddDialog() {
      this.$refs.addFormRef.resetFields();
    },
    editEvent(row) {
      this.editVisible = true;
      this.Divide = row;
      console.log(this.Divide, 111111)
    },
    changebz(row) {
      this.$confirm('将此人转为已治愈，是否继续？', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }
      ).then(() => {
        this.Divide = row;
        this.Divide.bz = "已治愈"
        this.$axios.post('/divide/updateDivide', this.Divide).then(res => {
          if (res.data.status === 10000) {
            this.$message.success('更新成功！')
            this.getDivideList();
          } else {
            this.$message.error('更新失败！')
          }
        })
      }).catch(() => {
        this.$message.info("已取消删除")
      })
    },
    ConfirmUpdate() {
      this.$axios.post('/divide/updateDivide', this.Divide).then(res => {
        if (res.data.status === 10000) {
          this.$message.success('更新成功！')
          this.getDivideList();
        } else {
          this.$message.error('更新失败！')
        }
      })
      this.editVisible = false;
    },
    closeEditDialog() {
      this.$refs.addFormRef.resetFields();
    },
    removeEvent(userId) {
      console.log('删除了这个', userId)
      this.$confirm('此操作将永久删除，是否继续？', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }
      ).then(() => {
        this.$axios.get('/admin/divide/deleteByUserId', {params: {userId}}).then(res => {
          this.$message.success('删除成功')
          this.getDivideList();
        })
      }).catch(() => {
        this.$message.info("已取消删除")

      })
    },
  },
  created() {
    this.getDivideList();
  }
}
</script>

<style scoped>

</style>
